<template>
  <div class="background">
    <el-image class="Logo" :src="src"></el-image>
    <div class="Login">
      <el-button v-if="loginif()" @click="Onclick()"
        >登录<i class="el-icon-s-custom el-icon--right"></i
      ></el-button>
      <el-dropdown v-if="!loginif()">
        <span class="el-dropdown-link">
          <el-image
            style="width: 50px; height: 50px"
            :src="src6"
            :fit="cover"
          ></el-image>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click.native="toback()" v-if="admin()"
            >管理后台</el-dropdown-item
          >
          <el-dropdown-item @click.native="PersonalSettings()"
            >个人设置</el-dropdown-item
          >
          <el-dropdown-item @click.native="loginexit()"
            >退出登录</el-dropdown-item
          >
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div class="Appointment" @click="toAppointment()">
      <el-image class="AppointmentIcon" :src="src2"></el-image>
    </div>
    <div class="Activity">
      <el-image
        class="ActivityIcon"
        :src="src3"
        @click="toActivity()"
      ></el-image>
    </div>
    <div class="Course">
      <el-image class="CourseIcon" :src="src4" @click="toCourse()"></el-image>
    </div>
    <div class="Lost">
      <el-image class="LostIcon" :src="src5" @click="toLost()"></el-image>
    </div>
  </div>
</template>

<style>
.background {
  background-color: #006699;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
}
.Logo {
  width: 270px;
  height: auto;
  margin-left: 80px;
  margin-top: 30px;
}
.Login {
  float: right;
  position: absolute;
  margin-top: -70px;
  margin-left: 1300px;
}
.Appointment {
  cursor: pointer;
  background-color: #cccc66;
  float: left;
  position: absolute;
  margin-top: 20px;
  margin-left: 350px;
  width: 350px;
  height: 200px;
  border-radius: 25px;
}
.AppointmentIcon {
  margin-top: 40px;
  margin-left: 40px;
  height: auto;
  width: auto;
}
.Course {
  cursor: pointer;
  background-color: #66ff66;
  float: left;
  position: absolute;
  margin-top: 280px;
  margin-left: 350px;
  width: 350px;
  height: 200px;
  border-radius: 25px;
}
.CourseIcon {
  margin-top: 40px;
  margin-left: 15px;
  height: auto;
  width: auto;
}
.Activity {
  cursor: pointer;
  background-color: #66cc66;
  float: left;
  position: absolute;
  margin-top: 20px;
  margin-left: 830px;
  width: 350px;
  height: 200px;
  border-radius: 25px;
}
.ActivityIcon {
  margin-top: 40px;
  margin-left: 10px;
  height: auto;
  width: auto;
}
.Lost {
  cursor: pointer;
  background-color: #ccff66;
  float: left;
  position: absolute;
  margin-top: 280px;
  margin-left: 830px;
  width: 350px;
  height: 200px;
  border-radius: 25px;
}
.LostIcon {
  margin-top: 30px;
  margin-left: 10px;
  height: auto;
  width: auto;
}
.el-dropdown-link {
  cursor: pointer;
  color: #409eff;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.alert {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 15px;
}
</style>

<script>
export default {
  data() {
    return {
      url:
        "https://dss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=4016333918,4269266815&fm=26&gp=0.jpg",
      src:
        "http://img.whitelion233.top/images/2020/03/25/logoa104e2210c1ef437.png",
      src2:
        "http://img.whitelion233.top/images/2020/03/25/b5e2f377cb062b9aab082db092191baa.png",
      src3:
        "http://img.whitelion233.top/images/2020/03/25/0255abf4e3eb77bd16318ce91ed350a3.png",
      src4:
        "http://img.whitelion233.top/images/2020/03/25/f01ad7345c95cc11e5df6256d47958c9.png",
      src5:
        "http://img.whitelion233.top/images/2020/03/25/fa21a8fc33a2630e529cbe90fc4067f0.png",
      src6:
        "http://img.whitelion233.top/images/2020/04/17/u38083990471382703100fm26gp0.png"
    };
  },
  methods: {
    toAppointment() {
      //先判断是否登陆，然后跳转路由
      if (localStorage.state >= 0) {
        this.$router.push("/Appointment/Print");
      } else {
        this.$alert("请完成登陆", "提示", {
          confirmButtonText: "确定",
          type: "warning",
          customClass: "alert",
          callback: action => {
            this.Onclick();
          }
        });
      }
    },
    toCourse() {
      //先判断是否登陆，然后跳转路由
      if (localStorage.state >= 0) {
        this.$router.push("/Course");
      } else {
        this.$alert("请完成登陆", "提示", {
          confirmButtonText: "确定",
          type: "warning",
          customClass: "alert",
          callback: action => {
            this.Onclick();
          }
        });
      }
    },
    toActivity() {
      //先判断是否登陆，然后跳转路由
      if (localStorage.state >= 0) {
        this.$router.push("/Activity");
      } else {
        this.$alert("请完成登陆", "提示", {
          confirmButtonText: "确定",
          type: "warning",
          customClass: "alert",
          callback: action => {
            this.Onclick();
          }
        });
      }
    },
    toLost() {
      //先判断是否登陆，然后跳转路由
      if (localStorage.state >= 0) {
        this.$router.push("/Lost/infind");
      } else {
        this.$alert("请完成登陆", "提示", {
          confirmButtonText: "确定",
          type: "warning",
          customClass: "alert",
          callback: action => {
            this.Onclick();
          }
        });
      }
    },
    Onclick() {
      //在延时加载界面后，跳转到登陆界面。
      const loading = this.$loading({
        lock: true,
        text: "正在加载",
        spinner: "el-icon-loading"
      });
      setTimeout(() => {
        loading.close();
        this.$router.push("/login");
      }, 1000);
    },
    loginexit() {
      //退出登陆
      localStorage.clear();
      location.reload();
      this.$router.push("/");
    },
    loginif() {
      //判断是否是登陆状态
      var loginif = localStorage.state / 2 - 2020514 + 2020;
      if (loginif >= 0) {
        //如果存在这个变量且大于0说明已经登陆
        var myDate = new Date();
        var timestroage = {
          Date: myDate.getDate(),
          Hours: myDate.getHours(),
          Minutes: myDate.getMinutes()
        }; //将星期日期，小时，分钟储存。
        localStorage.setItem("time", JSON.stringify(timestroage)); //动态变更时间，记录每次进入界面的时间，作为判断依据.
        if (localStorage.conut == null) {
          //一个flag.记录是否第一次登陆，如果是则记录下登陆时间。
          localStorage.savelogintime = localStorage.time;
          localStorage.conut = 1;
        }
        console.log(JSON.parse(localStorage.time));
        var time = JSON.parse(localStorage.time);
        var savelogintime = JSON.parse(localStorage.savelogintime);
        if (
          time.Minutes >= savelogintime.Minutes + 15 ||
          time.Hours > savelogintime.Hours ||
          time.Date > savelogintime.Date
        ) {
          //判断是否登陆超过15分钟或者超过一小时或超过一天
          localStorage.clear();
          this.$alert("登陆超时,请重新登陆！！", "提示", {
            confirmButtonText: "确定",
            type: "warning",
            customClass: "alert",
            callback: action => {
              this.$router.push("/");
            }
          });
        }
        return false;
      } else return true;
    },
    admin() {
      //检测是否是管理员
      var ifadmin = localStorage.state / 2 - 2020514 + 2020;
      if (ifadmin == 0) return false;
      else return true;
    },
    toback() {
      //前往后端的跳转函数，设定加载界面时长。
      const loading = this.$loading({
        lock: true,
        text: "正在加载",

        spinner: "el-icon-loading"
      });
      setTimeout(() => {
        loading.close();
        this.$router.push("/Backstage");
      }, 1000);
    },
    PersonalSettings() {
      //延时加载界面后，跳转到个人设置路由.
      const loading = this.$loading({
        lock: true,
        text: "正在加载",

        spinner: "el-icon-loading"
      });
      setTimeout(() => {
        loading.close();
        this.$router.push("/SetUp");
      }, 1000);
    }
  },
  created: function() {
    //初始化函数
    var useurl = localStorage.url; //重新读取头像url并保存，方便显示。
    if (useurl != null) {
      this.src6 = useurl;
      location.reload;
    }
  }
};
</script>
