<template>
  <div>
    <!-- 卡片视图 -->
    <el-card>
      <el-card shadow="always">
        <div>
          <el-row :gutter="20">
            <el-col :span="8">
              <!--  -->
              <el-card shadow="always">
                预约总数:{{ TotalNumberOfAppointments }}
              </el-card>
            </el-col>
            <el-col :span="8">
              <!--  -->
              <el-card shadow="always">
                目前打印店预约数量:{{ DTotal }}
              </el-card>
            </el-col>
            <el-col :span="8">
              <!--  -->
              <el-card shadow="always">
                目前运动场预约数量{{ YTotal }}
              </el-card>
            </el-col>
          </el-row>
        </div>
      </el-card>

      <div>
        <el-row :gutter="0">
          <el-col :span="12">
            <!--  -->
            <el-card shadow="always">
              <div id="main1" style="width: 100%;height:400px;"></div>
            </el-card>
          </el-col>
          <el-col :span="12">
            <!--  -->
            <el-card shadow="always">
              <div id="main2" style="width: 100%;height:400px;"></div>
            </el-card>
          </el-col>
        </el-row>
      </div>
    </el-card>
  </div>
</template>

<script>
//导入echarts
import echarts from "echarts";

export default {
  data() {
    return {
      TotalNumberOfAppointments: "",
      DTotal: "",
      YTotal: ""
    };
  },
  created() {
    this.$http
      .get("/api/articles/makeAnAppointment/core")
      .then(res => {
        console.log(res.data.data.length);
        if (res.data) {
          this.DTotal = res.data.data[0].DTotal;
          this.YTotal = res.data.data[0].YTotal;
          this.TotalNumberOfAppointments = this.DTotal + this.YTotal;

          console.log("更新成功");
        } else {
          console.log("错误");
          this.$notify({
            message: "更新失败",
            type: "warning"
          });
        }
      })
      .catch(rej => {
        this.$notify({
          message: rej,
          type: "warning"
        });
      });
  },
  async mounted() {
    // 基于准备好的dom，初始化echarts实例
    var myChart1 = echarts.init(document.getElementById("main1"));
    var myChart2 = echarts.init(document.getElementById("main2"));

    // 指定图表的配置项和数据

    const { data: res1 } = await this.$http.get(
      "api/articles/makeAnAppointment/core1"
    );

    const { data: res2 } = await this.$http.get(
      "api/articles/makeAnAppointment/core2"
    );

    // 使用刚指定的配置项和数据显示图表。
    myChart1.setOption(res1.data);
    myChart2.setOption(res2.data);
  }
};
</script>

<style></style>
