<template>
  <div>
    <!-- 卡片视图 -->
    <el-card>
      <div>
        <el-card shadow="always"> 活动总数:{{ TotalNumber }} </el-card>
        <!--
        <el-row :gutter="20">
          <el-col :span="8">
            <el-card shadow="always"> 活动总数:{{ TotalNumber }} </el-card>
          </el-col>
          <el-col :span="8">
            <el-card shadow="always"> 日均活动数量:{{ b }} </el-card>
          </el-col>
          <el-col :span="8">
            <el-card shadow="always"> 目前活动数量{{ c }} </el-card>
          </el-col>
        </el-row>
        -->
      </div>

      <!-- 为ECharts准备一个具备大小（宽高）的Dom -->
      <div id="main" style="width: 100%;height:560px;"></div>
    </el-card>
  </div>
</template>

<script>
//导入echarts
import echarts from "echarts";

export default {
  data() {
    return {
      TotalNumber: "",
      b: "10",
      c: "600"
    };
  },
  created() {
    this.$http
      .get("/api/articles/activity/administration")
      .then(res => {
        console.log(res.data.data.length);
        if (res.data) {
          this.TotalNumber = res.data.data.length;

          console.log("更新成功");
        } else {
          console.log("错误");
          this.$notify({
            message: "更新失败",
            type: "warning"
          });
        }
      })
      .catch(rej => {
        this.$notify({
          message: rej,
          type: "warning"
        });
      });
  },
  async mounted() {
    // 基于准备好的dom，初始化echarts实例
    var myChart = echarts.init(document.getElementById("main"));

    // 指定图表的配置项和数据
    const { data: res } = await this.$http.get("/api/articles/activity/core/T");

    // 使用刚指定的配置项和数据显示图表。
    myChart.setOption(res.data);
  }
};
</script>

<style></style>
