<template>
  <el-container>
    <el-header>
      <el-menu
        :default-active="activeIndex2"
        class="el-menu-demo"
        mode="horizontal"
        background-color="#b3c0d1"
        text-color="#545c64"
        router="true"
        active-text-color="#ffd04b"
      >
        <el-menu-item index="infind">正在寻找</el-menu-item>
        <el-menu-item index="add">发布招领</el-menu-item>
        <el-menu-item @click="onloading()">返回主页</el-menu-item>
      </el-menu>
    </el-header>
    <!-- @select="handleSelect" -->
    <el-main>
      <router-view></router-view>
    </el-main>
  </el-container>
</template>

<style>
.el-menu-demo {
  position: absolute;
  left: 650px;
}
.el-header {
  background-color: #545c64;
}
</style>

<script>
export default {
  data() {
    return {
      activeIndex2: "infind"
    };
  },
  methods: {
    onloading() {
      //跳转主页
      const loading = this.$loading({
        lock: true,
        text: "正在加载",
        spinner: "el-icon-loading"
      });
      setTimeout(() => {
        loading.close();
        this.$router.push("/");
      }, 1000);
    }
  },
  created: function() {
    //初始化界面
    if (localStorage.state == null) {
      //判断是否登陆。如果未登陆，弹出提示，跳转到登陆界面。
      this.$alert("请完成登陆", "提示", {
        confirmButtonText: "确定",
        type: "warning",
        customClass: "alert",
        callback: action => {
          this.$router.push("/login");
        }
      });
    }
  }
};
</script>
