<template>
  <el-container style="height:100%; border: 1px solid #eee">
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
      <el-image class="LogoAP" :src="src"></el-image>
      <el-menu :default-openeds="['1', '3']">
        <router-link to="/Appointment/Print">
          <el-menu-item index="1">
            <i class="el-icon-s-platform"></i>
            <span slot="title">打印预约</span>
          </el-menu-item>
        </router-link>
        <router-link to="/Appointment/Sport">
          <el-menu-item index="2">
            <i class="el-icon-basketball"></i>
            <span slot="title">运动场所预约</span>
          </el-menu-item>
        </router-link>
        <el-menu-item index="3" @click="onloading()">
          <i class="el-icon-s-home"></i>
          <span slot="title">返回主页</span>
        </el-menu-item>
      </el-menu>
    </el-aside>

    <el-container>
      <el-header style="text-align: right; font-size: 12px">
        <span></span>
      </el-header>

      <el-main>
        <router-view></router-view>
        <!-- 预留插槽，显示两个不同界面 -->
      </el-main>
    </el-container>
  </el-container>
</template>
<style>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
  height: 100%;
}
a {
  text-decoration: none;
}
.LogoAP {
  width: 135px;
  height: auto;
  margin-left: 25px;
  margin-top: 0px;
}
.router-link-active {
  text-decoration: none;
}
</style>

<script>
export default {
  data() {
    return {
      src:
        "http://img.whitelion233.top/images/2020/03/25/logoa104e2210c1ef437.png"
    };
  },
  methods: {
    onloading() {
      //返回主页函数。
      const loading = this.$loading({
        lock: true,
        text: "正在加载",
        spinner: "el-icon-loading"
      });
      setTimeout(() => {
        loading.close();
        this.$router.push("/");
      }, 1000);
    }
  },
  created: function() {
    //初始化函数，判断是否登陆，防止直接进入界面。
    if (localStorage.state == null) {
      this.$alert("请完成登陆", "提示", {
        confirmButtonText: "确定",
        type: "warning",
        customClass: "alert",
        callback: action => {
          this.$router.push("/login");
        }
      });
    }
  }
};
</script>
