<template>
  <el-container style="height: 100vh; border: 1px solid #eee">
    <el-aside
      :width="isCollapse ? '44px' : '200px'"
      style="el-icon-user-solid: rgb(238, 241, 246)"
    >
      <el-image class="LogoAP" :src="src"></el-image>
      <el-menu
        router
        :collapse="isCollapse"
        :collapse-transition="false"
        unique-opened="true"
      >
        <el-submenu index="1">
          <template slot="title"
            ><i class="el-icon-s-management"></i>拓展课程管理</template
          >
          <el-menu-item index="/articles/extendedCurriculum/core"
            >课程信息中心</el-menu-item
          >
          <el-menu-item index="/articles/extendedCurriculum/administration"
            >课程列表</el-menu-item
          >
        </el-submenu>
      </el-menu>
      <el-menu
        router
        :collapse="isCollapse"
        :collapse-transition="false"
        unique-opened="true"
      >
        <el-menu-item index="/articles/findLostItems/administration"
          ><i class="el-icon-sell"></i>失物招领信息管理</el-menu-item
        >
      </el-menu>
      <el-menu router :collapse="isCollapse" :collapse-transition="false">
        <el-submenu index="1">
          <template slot="title"
            ><i class="el-icon-s-flag"></i>校园活动管理</template
          >
          <el-menu-item index="/articles/activity/core"
            >活动信息数据概览</el-menu-item
          >
          <el-menu-item index="/articles/activity/administration"
            >活动列表</el-menu-item
          >
        </el-submenu>
      </el-menu>
      <el-menu router :collapse="isCollapse" :collapse-transition="false">
        <el-submenu index="1">
          <template slot="title"
            ><i class="el-icon-phone"></i>预约管理</template
          >
          <el-menu-item index="/articles/makeAnAppointment/core"
            >预约信息数据概览</el-menu-item
          >
          <el-menu-item index="/articles/makeAnAppointment/AppointmentPrint"
            >复印店预约列表</el-menu-item
          >
          <el-menu-item index="/articles/makeAnAppointment/AppointmentSport"
            >运动场预约列表</el-menu-item
          >
        </el-submenu>
      </el-menu>
      <el-menu
        router
        :default-openeds="['1']"
        :collapse="isCollapse"
        :collapse-transition="false"
        unique-opened="true"
      >
        <el-menu-item index="/articles/accountManagement/administration"
          ><i class="el-icon-user-solid"></i>账户列表</el-menu-item
        >
      </el-menu>
    </el-aside>

    <el-container>
      <el-header style="text-align: right; font-size: 12px">
        <div>
          <h1>智慧校园后台管理系统</h1>
        </div>
        <div>
          <el-button
            type="info"
            icon="el-icon-s-home"
            @click="homepage()"
            circle
          ></el-button>
        </div>
      </el-header>

      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<style>
html.body {
  padding: 0;
  margin: 0;
}
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
.LogoAP {
  width: 135px;
  height: auto;
  margin-left: 25px;
  margin-top: 0px;
}
.el-header {
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: rgb(97, 95, 93);
  font-size: 30px;
}
.toggle-button {
  background-color: lightblue;
  font-size: 10px;
  line-height: 24px;
  color: black;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
</style>

<script>
export default {
  data() {
    return {
      src:
        "http://img.whitelion233.top/images/2020/03/25/logoa104e2210c1ef437.png",
      login: true,
      //是否折叠
      isCollapse: false
    };
  },
  methods: {
    //点击按钮，切换猜到的折叠&&展开
    toggleCollapse() {
      this.isCollapse = !this.isCollapse;
    },
    homepage() {
      const loading = this.$loading({
        lock: true,
        text: "正在加载",

        spinner: "el-icon-loading"
      });
      setTimeout(() => {
        loading.close();
        this.$router.push("/");
      }, 1000);
    }
  },
  created: function() {
    this.$router.push("/articles/accountManagement/administration");
  }
};
</script>
