<template>
  <div>
    <!-- 卡片视图 -->
    <el-card>
      <div>
        <el-row :gutter="0">
          <el-col :span="8">
            <!-- 搜索框 -->
            <el-input
              placeholder="请输入物品信息"
              v-model="queryInfo.query"
              clearable
              @clear="Refresh()"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="Search()"
              ></el-button>
            </el-input>
          </el-col>
          <el-col :span="16"> </el-col>
        </el-row>
        <!-- 用户列表 -->
        <el-table :data="tableData" border stripe>
          <el-table-column type="index"></el-table-column>
          <el-table-column label="招领状态" prop="Lstate"></el-table-column>
          <el-table-column label="日期" prop="Ldate"></el-table-column>
          <el-table-column label="图片" width="180">
            <template slot-scope="scope">
              <el-image
                style="width: 100px; height: 100px"
                :src="tableData[scope.$index].Lurl"
                :preview-src-list="tableData[scope.$index].Lurl"
              >
              </el-image>
            </template>
          </el-table-column>
          <el-table-column label="物品类型" prop="Lname"></el-table-column>
          <el-table-column
            label="物品信息"
            prop="Linfomation"
          ></el-table-column>
          <el-table-column label="操作" width="150">
            <template slot-scope="scope">
              <!-- 修改按钮 -->
              <el-tooltip
                effect="dark"
                content="修改"
                placement="right-end"
                :enterable="false"
              >
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  circle
                  @click="showEditDialog(scope.row.id)"
                ></el-button>
              </el-tooltip>
              <!-- 删除按钮 -->
              <el-tooltip
                effect="dark"
                content="删除"
                placement="right-end"
                :enterable="false"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  circle
                  @click="deleteData(scope.row.id)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <!-- 分页 -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="PAGINATION.pagenum"
          :page-sizes="[5, 10, 100]"
          :page-size="PAGINATION.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="TotalNumberOfPages"
        >
        </el-pagination>
      </div>
    </el-card>

    <!-- 修改信息 -->
    <el-dialog
      title="修改"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!-- 内容主体 -->
      <span>
        <el-form
          :model="GtableData"
          :rules="rules"
          ref="GtableData"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="物品类型" prop="Lname">
            <el-input
              v-model="GtableData.Lname"
              maxlength="10"
              minlength="3"
              type="text"
              show-word-limit
            ></el-input>
          </el-form-item>
          <el-form-item label="拾取时间" required>
            <el-col :span="11">
              <el-form-item prop="Ldate">
                <el-date-picker
                  type="date"
                  placeholder="选择日期"
                  v-model="GtableData.Ldate"
                  style="width: 100%;"
                ></el-date-picker>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item label="联系方式" prop="Lcontact">
            <el-input type="text" v-model="GtableData.Lcontact"></el-input>
          </el-form-item>
          <el-form-item label="物品详情" prop="Linfomation">
            <el-input
              type="textarea"
              v-model="GtableData.Linfomation"
            ></el-input>
          </el-form-item>
        </el-form>
      </span>
      <!-- 底部内容 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="Change_2()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      //
      ruleForm: {
        Lstate: false,
        Ldate: "",
        Lname: "",
        Linfomation: "",
        Lcontact: "",
        Lurl: ""
      },

      //搜索实现
      queryInfo: {
        query: ""
      },

      //分页实现
      PAGINATION: {
        //当前的页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 5
      },

      //此时页面数据总数
      TotalNumberOfPages: 0,

      //修改
      GtableData: [],
      //----只读
      value: true,
      //动态获取id
      id_id: "",
      //修改界面的显示&&隐藏
      editDialogVisible: false,

      rules: {
        Lname: [
          { required: true, message: "请输入物品类型", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 10 个字符", trigger: "blur" }
        ],
        Ldate: [
          {
            type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change"
          }
        ],
        Lcontact: [
          { required: true, message: "请填写联系方式", trigger: "blur" }
        ],
        Linfomation: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.$http
      .get("/api/articles/findLostItems/administration", this.tableData)
      .then(res => {
        console.log(res.data.data.length);
        if (res.data) {
          this.TotalNumberOfPages = res.data.data.length;

          this.tableData = [];
          for (var i = 0; i < res.data.data.length; i++) {
            if (res.data.data[i].Lstate == 1) {
              res.data.data[i].Ldate = "未找到失主";
            } else res.data.data[i].Ldate = "已关闭";
            res.data.data[i].Ldate = res.data.data[i].Ldate.slice(0, 10);
            this.tableData.push(
              res.data.data[
                this.PAGINATION.pagesize * (this.PAGINATION.pagenum - 1) + i
              ]
            );
          }
        } else {
          console.log("错误");
          this.$notify({
            message: "更新失败",
            type: "warning"
          });
        }
      })
      .catch(rej => {
        this.$notify({
          message: rej,
          type: "warning"
        });
      });
  },
  methods: {
    //监听 ---- 改变
    handleSizeChange(newSize) {
      this.PAGINATION.pagesize = newSize;
      this.Refresh();
    },
    //监听 页码值 改变
    handleCurrentChange(newPage) {
      this.PAGINATION.pagenum = newPage;
      this.Refresh();
    },
    //监听 switch 开关的改变
    userStateChanged() {},
    //编辑对话框
    showEditDialog(id) {
      this.editDialogVisible = true;
      //后端调用数据
      this.Change(id);
    },
    //刷新页面
    Refresh() {
      this.$http
        .get("/api/articles/findLostItems/administration", this.tableData)
        .then(res => {
          console.log(res.data.data.length);
          if (res.data) {
            this.tableData = [];

            for (var i = 0; i < res.data.data.length; i++) {
              res.data.data[i].Ldate = res.data.data[i].Ldate.slice(0, 10);
              this.tableData.push(
                res.data.data[
                  this.PAGINATION.pagesize * (this.PAGINATION.pagenum - 1) + i
                ]
              );
            }

            this.TotalNumberOfPages = res.data.data.length;

            console.log(this.tableData);
            console.log("更新成功");
          } else {
            console.log("错误");
            this.$notify({
              message: "更新失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //删除
    deleteData(ID) {
      this.$http
        .delete("/api/articles/findLostItems/administration/" + ID)
        .then(res => {
          if (res.data) {
            console.log("删除成功");
            this.Refresh();
          } else {
            console.log("错误");
            this.$notify({
              message: "删除失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //修改目标的数据
    Change(id) {
      this.$http
        .get("/api/articles/findLostItems/administration")
        .then(res => {
          if (res.data) {
            for (var i = 0; i < res.data.data.length; i++) {
              if (res.data.data[i].id == id) {
                this.GtableData = res.data.data[i];
                this.id_id = id;
              }
            }
            console.log(this.GtableData);
            console.log("待修改信息加载成功");
          } else {
            console.log("错误");
            this.$notify({
              message: "待修改信息加载失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    Change_2() {
      this.$http
        .post(
          "/api/articles/findLostItems/administration/" + this.id_id,
          this.GtableData
        )
        .then(res => {
          if (res.data) {
            console.log("修改成功");
            this.editDialogVisible = false;
            this.Refresh();
          } else {
            console.log("错误");
            this.$notify({
              message: "修改失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //搜索
    Search() {
      this.$http
        .post(
          "/api/articles/findLostItems/search/administration",
          this.queryInfo
        )
        .then(res => {
          if (res.data) {
            console.log("搜索成功");

            this.tableData = res.data.data;
          } else {
            console.log("错误");
            this.$notify({
              message: "搜索失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    }
  }
};
</script>

<style></style>
