<template>
  <div class="login_container" id="background">
    <div class="login_box">
      <!-- 头像区-->
      <div class="avatar_box">
        <img src="../../assets/logo.png" alt="" />
      </div>
      <!--登录表单区-->
      <el-form
        ref="loginFormRef"
        :model="loginForm"
        :rules="loginFormRules"
        label-width="0px"
        class="login_form"
      >
        <!--用户名-->
        <el-form-item prop="ID">
          <el-input
            v-model="loginForm.ID"
            prefix-icon="el-icon-user-solid"
            placeholder="请输入学号"
          ></el-input>
        </el-form-item>
        <!--密码-->
        <el-form-item prop="password">
          <el-input
            v-model="loginForm.password"
            prefix-icon="el-icon-s-goods"
            type="password"
            placeholder="请输入登录密码"
          ></el-input>
        </el-form-item>
        <!--按钮区域-->
        <el-form-item class="btns">
          <el-button type="primary" @click="login()">登录</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
const jwt = require("jsonwebtoken");
const secret = "smartcompus";
export default {
  data() {
    return {
      //这是一个登录表单的数据绑定对象
      loginForm: {
        ID: "",
        password: "",
        url: ""
      },

      //这是表单的验证规则
      loginFormRules: {
        //验证用户是否合法
        ID: [
          { required: true, message: "请输入登录名称", trigger: "blur" },
          { min: 3, max: 10, message: "长度在 3 到 10 个字符", trigger: "blur" }
        ],
        //验证密码是否合法
        password: [
          { required: true, message: "请输入登录密码", trigger: "blur" },
          { min: 6, max: 15, message: "长度在 6 到 15 个字符", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    login() {
      //登陆函数，发送用户输入的ID和密码给后端，后端比较后，返回加密的权限，ID，和头像url.
      this.$http
        .post("/api/login", {
          ID: this.loginForm.ID,
          password: this.loginForm.password
        })
        .then(res => {
          if (res.data.key != null) {
            localStorage.setItem("state", res.data.key);
            localStorage.setItem("ID", res.data.ID);
            if (res.data.url != null) {
              //如果用户有自定义头像，记录值，没有则不记录。
              localStorage.setItem("url", res.data.url);
            }
            this.$router.push("/");
          } else {
            console.log("错误");
            this.$notify({
              message: "账号/密码错误",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    }
  }
};
</script>

<style lang="less" scoped>
#background {
  background-color: #006699;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
}
.login_container {
  background-color: #2b4b6b;
  height: 100%;
}
.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  .avatar_box {
    height: 103px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}
.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 80px;
  box-sizing: border-box;
}

.btns {
  display: flex;
  justify-content: center;
}
</style>
