<template>
  <div>
    <!-- 卡片视图 -->
    <el-card>
      <div>
        <!--  -->
        <el-card shadow="always"> 失物总数:{{ TotalLostProperty }} </el-card>
      </div>

      <div>
        <el-row :gutter="20">
          <el-col :span="8">
            <!--  -->
            <el-card shadow="always">
              <div id="main1" style="width: 100%;height:360px;"></div>
            </el-card>
            <el-card shadow="always"> 已领取：{{ HaveTaken }} </el-card>
            <el-card shadow="always"> 未领取：{{ NotTake }} </el-card>
          </el-col>
          <el-col :span="16">
            <!--  -->
            <el-card shadow="always">
              <div id="main2" style="width: 100%;height:560px;"></div>
            </el-card>
          </el-col>
        </el-row>
      </div>
    </el-card>
  </div>
</template>

<script>
//导入echarts
import echarts from "echarts";

export default {
  data() {
    return {
      TotalLostProperty: "",
      HaveTaken: 0,
      NotTake: 0
    };
  },
  created() {},
  methods: {},
  async mounted() {
    // 基于准备好的dom，初始化echarts实例
    var myChart1 = echarts.init(document.getElementById("main1"));
    var myChart2 = echarts.init(document.getElementById("main2"));

    // 指定图表的配置项和数据

    const { data: res1 } = await this.$http.get(
      "api/articles/findLostItems/core"
    );

    this.HaveTaken = res1.data.series[0].data[0].value;
    this.NotTake = res1.data.series[0].data[1].value;
    this.TotalLostProperty = this.HaveTaken + this.NotTake;

    const { data: res2 } = await this.$http.get(
      "/api/articles/findLostItems/core/T"
    );

    // 使用刚指定的配置项和数据显示图表。
    myChart1.setOption(res1.data);
    myChart2.setOption(res2.data);
  }
};
</script>

<style></style>
