<template>
  <div>
    <!-- 卡片视图 -->
    <el-card>
      <div>
        <el-row :gutter="20">
          <el-col :span="8">
            <!--  -->
            <el-card shadow="always"> 课程总数:{{ Total }} </el-card>
          </el-col>
          <el-col :span="16">
            <!--  -->
            <el-card shadow="always">
              课程来源比例分析图
            </el-card>
          </el-col>
        </el-row>
      </div>

      <div>
        <el-row :gutter="20">
          <el-col :span="8">
            <el-card
              shadow="always"
              v-for="item of curriculum"
              :key="item.data"
            >
              {{ item.data1 }}:{{ item.data2 }}
            </el-card>
          </el-col>
          <el-col :span="16">
            <!--  -->
            <el-card shadow="always">
              <!-- 为ECharts准备一个具备大小（宽高）的Dom -->
              <div id="main" style="width: 100%;height:560px;"></div>
            </el-card>
          </el-col>
        </el-row>
      </div>
    </el-card>
  </div>
</template>

<script>
//导入echarts
import echarts from "echarts";

export default {
  data() {
    return {
      Total: "",
      curriculum: []
    };
  },
  created: function() {
    this.$http
      .get("/api/articles/extendedCurriculum/administration")
      .then(res => {
        console.log(res.data.data.length);
        if (res.data) {
          this.Total = res.data.data.length;

          console.log("更新成功");
        } else {
          console.log("错误");
          this.$notify({
            message: "更新失败",
            type: "warning"
          });
        }
      })
      .catch(rej => {
        this.$notify({
          message: rej,
          type: "warning"
        });
      });
  },
  async mounted() {
    // 基于准备好的dom，初始化echarts实例
    var myChart = echarts.init(document.getElementById("main"));

    // 指定图表的配置项和数据

    const { data: res } = await this.$http.get(
      "/api/articles/extendedCurriculum/core"
    );

    var _data_ = [];

    for (var i = 0; i < res.data.series[0].data.length; i++) {
      _data_.push({ data1: 1, data2: 1 });
      _data_[i].data2 = res.data.series[0].data[i].value;
      _data_[i].data1 = res.data.series[0].data[i].name;
      this.curriculum.push(_data_[i]);
    }

    // 使用刚指定的配置项和数据显示图表。
    myChart.setOption(res.data);
  }
};
</script>

<style></style>
