<template>
  <el-container style="height:100%; border: 1px solid #eee">
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
      <el-image class="LogoAP" :src="src"></el-image>
      <el-menu :default-openeds="['1', '3']">
        <el-menu-item index="1">
          <i class="el-icon-date"></i>
          <span slot="title">活动列表</span>
        </el-menu-item>
        <el-menu-item index="2" @click="onloading()">
          <i class="el-icon-s-home"></i>
          <span slot="title">返回主页</span>
        </el-menu-item>
      </el-menu>
    </el-aside>

    <el-container>
      <el-header style="text-align: right; font-size: 12px">
        <span></span>
      </el-header>

      <el-main>
        <el-button type="primary" @click="refresh()">刷新</el-button>
        <el-table
          :data="tableData"
          style="width: 100%"
          :default-sort="{ prop: 'date', order: 'descending' }"
        >
          <el-table-column prop="id" label="序号" sortable width="180">
          </el-table-column>
          <el-table-column prop="Adate" label="日期" sortable width="180">
          </el-table-column>
          <el-table-column prop="Aname" label="活动名" sortable width="180">
          </el-table-column>
          <el-table-column
            prop="Aaddress"
            label="活动地址"
            :formatter="formatter"
          >
          </el-table-column>
          <el-table-column
            prop="Ainfomation"
            label="活动内容"
            :formatter="formatter"
          >
          </el-table-column>
          <el-table-column prop="Atime" label="进行时间" width="180">
          </el-table-column>
        </el-table>
      </el-main>
    </el-container>
  </el-container>
</template>
<style>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
  height: 100%;
}
a {
  text-decoration: none;
}
.LogoAP {
  width: 135px;
  height: auto;
  margin-left: 25px;
  margin-top: 0px;
}
.router-link-active {
  text-decoration: none;
}
</style>

<script>
export default {
  data() {
    return {
      src:
        "http://img.whitelion233.top/images/2020/03/25/logoa104e2210c1ef437.png",
      tableData: []
    };
  },
  methods: {
    onloading() {
      //设置延迟，进行加载页面，之后跳转路由到首页
      const loading = this.$loading({
        lock: true,
        text: "正在加载",
        spinner: "el-icon-loading"
      });
      setTimeout(() => {
        loading.close();
        this.$router.push("/");
      }, 1000);
    },
    refresh() {
      //刷新数据，提交请求给后端，后端返回表单数据。
      this.$http
        .post("/api/activity", this.tableData)
        .then(res => {
          if (res.data) {
            var obj = {};
            for (var i = 0; i < res.data.data.length; i++) {
              res.data.data[i].Adate = res.data.data[i].Adate.slice(0, 10);
              obj[i] = res.data.data[i];
              if (this.tableData[i].id != res.data.data[i].id) {
                this.tableData.push(obj[i]);
                console.log("已添加");
              } else console.log("数据重复");
            }

            console.log("更新成功");
          } else {
            console.log("错误");
            this.$notify({
              message: "更新失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    }
  },
  created: function() {
    //页面初始化
    if (localStorage.state >= 0) {
      //进入界面则进行一次表单获取，通过请求后端。并且判断是否登陆，防止直接进入。
      this.$http
        .post("/api/activity", this.tableData)
        .then(res => {
          if (res.data) {
            var obj = {};
            for (var i = 0; i < res.data.data.length; i++) {
              res.data.data[i].Adate = res.data.data[i].Adate.slice(0, 10); //数据库时间格式有问题，进行分割后，存入原表单。
              obj[i] = res.data.data[i];
              this.tableData.push(obj[i]); //将表单填入数据表中显示。
              console.log("初始化成功");
            }

            console.log("更新成功");
          } else {
            console.log("错误");
            this.$notify({
              message: "更新失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    } else {
      this.$alert("请完成登陆", "提示", {
        confirmButtonText: "确定",
        type: "warning",
        customClass: "alert",
        callback: action => {
          this.$router.push("/login");
        }
      });
    }
  }
};
</script>
