<template>
  <div>
    <!-- 卡片视图 -->
    <el-card>
      <div>
        <el-row :gutter="20">
          <el-col :span="7">
            <!-- 搜索框 -->
            <el-input
              placeholder="请输入账户名称"
              v-model="queryInfo.query"
              clearable
              @clear="Refresh()"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="Search()"
              ></el-button>
            </el-input>
          </el-col>
          <el-col :span="4">
            <!-- 添加用户按钮 -->
            <el-button type="primary" @click="add = true">添加用户</el-button>
          </el-col>
        </el-row>
        <!-- 用户列表 -->
        <el-table :data="tableData" border stripe>
          <el-table-column type="index"></el-table-column>
          <el-table-column label="账户名称" prop="username"></el-table-column>
          <el-table-column label="账户密码" prop="password"></el-table-column>
          <el-table-column label="学号" prop="ID"></el-table-column>
          <el-table-column label="账户邮箱" prop="Email"></el-table-column>
          <el-table-column label="操作" width="150">
            <template slot-scope="scope">
              <!-- 修改按钮 -->
              <el-tooltip
                effect="dark"
                content="修改"
                placement="right-end"
                :enterable="false"
              >
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  circle
                  @click="showEditDialog(scope.row.ID)"
                ></el-button>
              </el-tooltip>
              <!-- 删除按钮 -->
              <el-tooltip
                effect="dark"
                content="删除"
                placement="right-end"
                :enterable="false"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  circle
                  @click="deleteData(scope.row.ID)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <!-- 分页 -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="PAGINATION.pagenum"
          :page-sizes="[5, 10, 100]"
          :page-size="PAGINATION.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="TotalNumberOfPages"
        >
        </el-pagination>
      </div>
    </el-card>

    <!-- 添加用户 -->
    <el-dialog
      title="添加用户"
      :visible.sync="add"
      width="50%"
      @close="addDialogClosed"
    >
      <!-- 内容主体 -->
      <span>
        <el-form
          :model="userlistAdd"
          :rules="rules"
          ref="userlistAdd"
          label-width="80px"
        >
          <el-form-item label="账户名称" prop="username">
            <el-input v-model="userlistAdd.username"></el-input>
          </el-form-item>
          <el-form-item label="账户密码" prop="password">
            <el-input v-model="userlistAdd.password"></el-input>
          </el-form-item>
          <el-form-item label="学号" prop="ID">
            <el-input v-model="userlistAdd.ID"></el-input>
          </el-form-item>
          <el-form-item label="账户邮箱" prop="Email">
            <el-input v-model="userlistAdd.Email"></el-input>
          </el-form-item>
        </el-form>
      </span>
      <!-- 底部内容 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="add = false">取 消</el-button>
        <el-button type="primary" @click="addUser()">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 修改用户信息 -->
    <el-dialog
      title="修改用户"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!-- 内容主体 -->
      <span>
        <el-form
          :model="GtableData"
          :rules="rules"
          ref="GtableData"
          label-width="80px"
        >
          <el-form-item label="账户名称" prop="username">
            <el-input v-model="GtableData.username"></el-input>
          </el-form-item>
          <el-form-item label="账户密码" prop="password">
            <el-input v-model="GtableData.password"></el-input>
          </el-form-item>
          <el-form-item label="学号">
            <el-input v-model="GtableData.ID" disabled></el-input>
          </el-form-item>
          <el-form-item label="账户邮箱" prop="Email">
            <el-input v-model="GtableData.Email"></el-input>
          </el-form-item>
        </el-form>
      </span>
      <!-- 底部内容 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="Change_2()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    //验证邮箱的规则
    var checkEmail = (rule, value, cd) => {
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;

      if (regEmail.test(value)) {
        //合法的邮箱
        return cd();
      }

      cd(new Error("请输入合法的邮箱"));
    };

    return {
      tableData: [],

      //添加用户
      userlistAdd: {
        username: "",
        password: "",
        ID: "",
        Email: ""
      },

      //搜索实现
      queryInfo: {
        query: ""
      },
      //此时页面数据总数
      TotalNumberOfPages: 0,

      //分页实现
      PAGINATION: {
        //当前的页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 5
      },

      //修改用户
      GtableData: [],
      //----只读
      value: true,
      //动态获取id
      id_id: "",

      //用户添加界面的显示&&隐藏
      add: false,
      //修改用户界面的显示&&隐藏
      editDialogVisible: false,
      //添加用户校验规则
      rules: {
        username: [
          { required: true, message: "请输入账户名称", trigger: "blur" },
          { min: 2, max: 12, message: "长度在 2 到 12 个字符", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入账户密码", trigger: "blur" },
          { min: 6, max: 15, message: "长度在 6 到 15 个字符", trigger: "blur" }
        ],
        ID: [
          { required: true, message: "请输入学号", trigger: "blur" },
          { min: 10, max: 10, message: "请输入正确的学号", trigger: "blur" }
        ],
        Email: [
          { required: true, message: "请输入账户邮箱", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.$http
      .get("/api/articles/accountManagement/administration", this.tableData)
      .then(res => {
        console.log(res.data.data.length);
        if (res.data) {
          this.TotalNumberOfPages = res.data.data.length;

          this.tableData = [];

          var j = this.PAGINATION.pagesize;
          if (
            this.PAGINATION.pagenum * this.PAGINATION.pagesize >
            res.data.data.length
          ) {
            j = res.data.data.length % this.PAGINATION.pagesize;
          }
          for (var i = 0; i < j; i++) {
            this.tableData.push(
              res.data.data[
                this.PAGINATION.pagesize * (this.PAGINATION.pagenum - 1) + i
              ]
            );
          }

          console.log(this.tableData);
          console.log("更新成功");
        } else {
          console.log("错误");
          this.$notify({
            message: "更新失败",
            type: "warning"
          });
        }
      })
      .catch(rej => {
        this.$notify({
          message: rej,
          type: "warning"
        });
      });
  },
  methods: {
    //监听 ---- 改变
    handleSizeChange(newSize) {
      this.PAGINATION.pagesize = newSize;
      this.Refresh();
    },
    //监听 页码值 改变
    handleCurrentChange(newPage) {
      this.PAGINATION.pagenum = newPage;
      this.Refresh();
    },
    //监听 switch 开关的改变
    userStateChanged() {},
    //添加用户对话框的关闭事件
    addDialogClosed() {
      this.$refs.userlistAdd.resetFields();
    },
    //刷新页面
    Refresh() {
      this.$http
        .get("/api/articles/accountManagement/administration", this.tableData)
        .then(res => {
          console.log(res.data.data.length);
          if (res.data) {
            this.tableData = [];

            var j = this.PAGINATION.pagesize;
            if (
              this.PAGINATION.pagenum * this.PAGINATION.pagesize >
              res.data.data.length
            ) {
              j = res.data.data.length % this.PAGINATION.pagesize;
            }
            for (var i = 0; i < j; i++) {
              this.tableData.push(
                res.data.data[
                  this.PAGINATION.pagesize * (this.PAGINATION.pagenum - 1) + i
                ]
              );
            }

            this.TotalNumberOfPages = res.data.data.length;

            console.log(this.tableData);
            console.log("更新成功");
          } else {
            console.log("错误");
            this.$notify({
              message: "更新失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //点击按钮，添加新--
    addUser() {
      this.$refs.userlistAdd.validate(valid => {
        if (!valid) return;
        //发起添加-post
        this.$http
          .post(
            "/api/articles/accountManagement/administration",
            this.userlistAdd
          )
          .then(res => {
            console.log(res);
            if (res.data) {
              console.log(res.data);
              this.add = false;
              this.Refresh();
            } else {
              console.log("错误");
              this.$notify({
                message: "数据错误",
                type: "warning"
              });
            }
          })
          .catch(rej => {
            this.$notify({
              message: rej,
              type: "warning"
            });
          });
      });
    },
    //编辑对话框
    showEditDialog(id) {
      this.editDialogVisible = true;
      //后端调用数据
      this.Change(id);
    },
    //删除
    deleteData(ID) {
      this.$http
        .delete("/api/articles/accountManagement/administration/" + ID)
        .then(res => {
          if (res.data) {
            console.log("删除成功");
            this.Refresh();
          } else {
            console.log("错误");
            this.$notify({
              message: "删除失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //修改目标的数据
    Change(id) {
      this.$http
        .get("/api/articles/accountManagement/administration")
        .then(res => {
          if (res.data) {
            for (var i = 0; i < res.data.data.length; i++) {
              if (res.data.data[i].ID == id) {
                this.GtableData = res.data.data[i];
                this.id_id = id;
              }
            }
            console.log(this.GtableData);
            console.log("待修改信息加载成功");
          } else {
            console.log("错误");
            this.$notify({
              message: "待修改信息加载失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    Change_2() {
      this.$http
        .post(
          "/api/articles/accountManagement/administration/" + this.id_id,
          this.GtableData
        )
        .then(res => {
          if (res.data) {
            console.log("修改成功");
            this.editDialogVisible = false;
            this.Refresh();
          } else {
            console.log("错误");
            this.$notify({
              message: "修改失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //搜索
    Search() {
      this.$http
        .post(
          "/api/articles/accountManagement/search/administration",
          this.queryInfo
        )
        .then(res => {
          if (res.data) {
            console.log("搜索成功");

            this.tableData = res.data.data;
          } else {
            console.log("错误");
            this.$notify({
              message: "搜索失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    }
  }
};
</script>

<style></style>
