<template>
  <div>
    <!-- 卡片视图 -->
    <el-card>
      <div>
        <el-row :gutter="20">
          <el-col :span="7">
            <!-- 搜索框 -->
            <el-input
              placeholder="请输入课程名称"
              v-model="queryInfo.query"
              clearable
              @clear="Refresh()"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="Search()"
              ></el-button>
            </el-input>
          </el-col>
          <el-col :span="4">
            <!-- 添加课程 -->
            <el-button type="primary" @click="add = true">添加课程</el-button>
          </el-col>
        </el-row>
        <!-- 用户列表 -->
        <el-table :data="tableData" border stripe>
          <el-table-column type="index"></el-table-column>
          <el-table-column label="日期" prop="Cdate"></el-table-column>
          <el-table-column label="课程名" prop="Cname"></el-table-column>
          <el-table-column label="开课地址" prop="Caddress"></el-table-column>
          <el-table-column
            label="活动内容"
            prop="Cinfomation"
          ></el-table-column>
          <el-table-column label="操作" width="180">
            <template slot-scope="scope">
              <!-- 修改按钮 -->
              <el-tooltip
                effect="dark"
                content="修改"
                placement="right-end"
                :enterable="false"
              >
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  circle
                  @click="showEditDialog(scope.row.id)"
                ></el-button>
              </el-tooltip>
              <!-- 删除按钮 -->
              <el-tooltip
                effect="dark"
                content="删除"
                placement="right-end"
                :enterable="false"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  circle
                  @click="deleteData(scope.row.id)"
                ></el-button>
              </el-tooltip>
              <!-- 详情按钮 -->
              <el-tooltip
                effect="dark"
                content="查看详情"
                placement="right-end"
                :enterable="false"
              >
                <el-button
                  icon="el-icon-document"
                  circle
                  type="success"
                  @click="Onclick(tableData[scope.$index].Csrc)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <!-- 分页 -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="PAGINATION.pagenum"
          :page-sizes="[5, 10, 100]"
          :page-size="PAGINATION.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="TotalNumberOfPages"
        >
        </el-pagination>
      </div>
    </el-card>

    <!-- 添加 -->
    <el-dialog
      title="添加"
      :visible.sync="add"
      width="50%"
      @close="addDialogClosed"
    >
      <!-- 内容主体 -->
      <span>
        <el-form
          :model="tableDataAdd"
          :rules="rules"
          ref="tableDataAdd"
          label-width="150px"
          class="demo-ruleForm"
        >
          <el-form-item label="课程名称" prop="Cname">
            <el-input v-model="tableDataAdd.Cname"></el-input>
          </el-form-item>
          <el-form-item label="上课地点" prop="Caddress">
            <el-input v-model="tableDataAdd.Caddress"></el-input>
          </el-form-item>
          <el-form-item label="课程日期" prop="Cdate">
            <el-date-picker
              v-model="tableDataAdd.Cdate"
              type="date"
              placeholder="选择日期"
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item label="课程内容" prop="Cinfomation">
            <el-input v-model="tableDataAdd.Cinfomation"></el-input>
          </el-form-item>
          <el-form-item label="详情地址" prop="Csrc">
            <el-input v-model="tableDataAdd.Csrc"></el-input>
          </el-form-item>
        </el-form>
      </span>
      <!-- 底部内容 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="add = false">取 消</el-button>
        <el-button type="primary" @click="addUser()">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 修改信息 -->
    <el-dialog
      title="修改"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!-- 内容主体 -->
      <span>
        <el-form
          :model="GtableData"
          :rules="rules"
          ref="GtableData"
          label-width="150px"
          class="demo-ruleForm"
        >
          <el-form-item label="课程名称" prop="Cname">
            <el-input v-model="GtableData.Cname"></el-input>
          </el-form-item>
          <el-form-item label="上课地点" prop="Caddress">
            <el-input v-model="GtableData.Caddress"></el-input>
          </el-form-item>
          <el-form-item label="课程日期" prop="Cdate">
            <el-date-picker
              v-model="GtableData.Cdate"
              type="date"
              placeholder="选择日期"
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item label="课程内容" prop="Cinfomation">
            <el-input v-model="GtableData.Cinfomation"></el-input>
          </el-form-item>
          <el-form-item label="详情地址" prop="Csrc">
            <el-input v-model="GtableData.Csrc"></el-input>
          </el-form-item>
        </el-form>
      </span>
      <!-- 底部内容 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="Change_2()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      tableDataAdd: {
        Cdate: "",
        Cname: "",
        Caddress: "",
        Cinfomation: "",
        Csrc: ""
      },

      //搜索实现
      queryInfo: {
        query: ""
      },

      //分页实现
      PAGINATION: {
        //当前的页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 5
      },

      //此时页面数据总数
      TotalNumberOfPages: 0,

      //修改
      GtableData: [],
      //----只读
      value: true,
      //动态获取id
      id_id: "",
      //添加界面的显示&&隐藏
      add: false,
      //修改界面的显示&&隐藏
      editDialogVisible: false,
      rules: {
        Cname: [
          { required: true, message: "请输入课程名称", trigger: "blur" },
          { min: 2, max: 10, message: "长度在 2 到 10 个字符", trigger: "blur" }
        ],
        Caddress: [
          { required: true, message: "请输入授课地点", trigger: "blur" }
        ],
        Cdate: [{ required: true, message: "请输入授课日期", trigger: "blur" }],
        Cinfomation: [
          { required: true, message: "请输入授课内容", trigger: "blur" }
        ],
        Csrc: [
          { required: true, message: "请输入授课详情地址", trigger: "change" }
        ]
      }
    };
  },
  created: function() {
    this.$http
      .get("/api/articles/extendedCurriculum/administration", this.tableData)
      .then(res => {
        console.log(res.data.data.length);
        if (res.data) {
          var obj = {};
          for (var i = 0; i < res.data.data.length; i++) {
            if (this.tableData[i] != res.data.data[i]) {
              res.data.data[i].Cdate = res.data.data[i].Cdate.slice(0, 10);
              obj[i] = res.data.data[i];
              this.tableData.push(obj[i]);
            }
          }
          console.log(this.tableData);
          console.log("更新成功");
        } else {
          console.log("错误");
          this.$notify({
            message: "更新失败",
            type: "warning"
          });
        }
      })
      .catch(rej => {
        this.$notify({
          message: rej,
          type: "warning"
        });
      });
  },
  methods: {
    //监听 ---- 改变
    handleSizeChange(newSize) {
      this.PAGINATION.pagesize = newSize;
      this.Refresh();
    },
    //监听 页码值 改变
    handleCurrentChange(newPage) {
      this.PAGINATION.pagenum = newPage;
      this.Refresh();
    },
    //打开详情网页
    Onclick(Csrc) {
      window.open(Csrc);
    },
    //添加--对话框的关闭事件
    addDialogClosed() {
      this.$refs.tableDataAdd.resetFields();
    },
    //刷新页面
    Refresh() {
      this.$http
        .get("/api/articles/extendedCurriculum/administration", this.tableData)
        .then(res => {
          console.log(res.data.data.length);
          if (res.data) {
            this.tableData = [];

            var j = this.PAGINATION.pagesize;
            if (
              this.PAGINATION.pagenum * this.PAGINATION.pagesize >
              res.data.data.length
            ) {
              j = res.data.data.length % this.PAGINATION.pagesize;
            }
            for (var i = 0; i < j; i++) {
              this.tableData.push(
                res.data.data[
                  this.PAGINATION.pagesize * (this.PAGINATION.pagenum - 1) + i
                ]
              );
            }

            this.TotalNumberOfPages = res.data.data.length;

            console.log(this.tableData);
            console.log("更新成功");
          } else {
            console.log("错误");
            this.$notify({
              message: "更新失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //点击按钮，添加新--
    addUser() {
      this.$refs.tableDataAdd.validate(valid => {
        if (!valid) return;
        //发起添加-post请求
        //------
        this.$http
          .post(
            "/api/articles/extendedCurriculum/administration",
            this.tableDataAdd
          )
          .then(res => {
            console.log(res);
            if (res.data) {
              console.log(res.data);
              this.add = false;
              this.Refresh();
            } else {
              console.log("错误");
              this.$notify({
                message: "数据错误",
                type: "warning"
              });
            }
          })
          .catch(rej => {
            this.$notify({
              message: rej,
              type: "warning"
            });
          });
      });
    },
    //编辑对话框
    showEditDialog(id) {
      this.editDialogVisible = true;
      //后端调用数据
      this.Change(id);
    },
    //删除
    deleteData(ID) {
      this.$http
        .delete("/api/articles/extendedCurriculum/administration/" + ID)
        .then(res => {
          if (res.data) {
            console.log("删除成功");
            this.Refresh();
          } else {
            console.log("错误");
            this.$notify({
              message: "删除失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //修改目标的数据
    Change(id) {
      this.$http
        .get("/api/articles/extendedCurriculum/administration")
        .then(res => {
          if (res.data) {
            for (var i = 0; i < res.data.data.length; i++) {
              if (res.data.data[i].id == id) {
                this.GtableData = res.data.data[i];
                this.id_id = id;
              }
            }
            console.log(this.GtableData);
            console.log("待修改信息加载成功");
          } else {
            console.log("错误");
            this.$notify({
              message: "待修改信息加载失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    Change_2() {
      this.$http
        .post(
          "/api/articles/extendedCurriculum/administration/" + this.id_id,
          this.GtableData
        )
        .then(res => {
          if (res.data) {
            console.log("修改成功");
            this.editDialogVisible = false;
            this.Refresh();
          } else {
            console.log("错误");
            this.$notify({
              message: "修改失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //搜索
    Search() {
      this.$http
        .post(
          "/api/articles/extendedCurriculum/search/administration",
          this.queryInfo
        )
        .then(res => {
          if (res.data) {
            console.log("搜索成功");

            this.tableData = res.data.data;
          } else {
            console.log("错误");
            this.$notify({
              message: "搜索失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    }
  }
};
</script>

<style></style>
