import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Appointment from "../views/Appointment/Appointment.vue";
import AppointmentPrint from "../views/Appointment/AppointmentPrint.vue";
import AppointmentSport from "../views/Appointment/AppointmentSport.vue";
import Activity from "../views/Activity/Activity.vue";
import Course from "../views/Course/Course.vue";
import Lost from "../views/Lost/Lost.vue";
import Lostinfind from "../views/Lost/infind.vue";
import add from "../views/Lost/add.vue";
import Login from "../views/Login/Login.vue";
import Backstage from "../views/Backstage.vue";
//预约
import makeAnAppointment_core from "@/views/Backstage/makeAnAppointment/core";
import makeAnAppointment_AppointmentPrint from "@/views/Backstage/makeAnAppointment/AppointmentPrint";
import makeAnAppointment_AppointmentSport from "@/views/Backstage/makeAnAppointment/AppointmentSport";
//账户管理
import accountManagement_administration from "@/views/Backstage/accountManagement/administration";
//活动
import activity_core from "@/views/Backstage/activity/core";
import activity_administration from "@/views/Backstage/activity/administration";
//拓张课程
import extendedCurriculum_core from "@/views/Backstage/extendedCurriculum/core";
import extendedCurriculum_administration from "@/views/Backstage/extendedCurriculum/administration";
//失物招领
import findLostItems_core from "@/views/Backstage/findLostItems/core";
import findLostItems_administration from "@/views/Backstage/findLostItems/administration";
//个人设置
import SetUp from "@/views/PersonalSettings/SetUp";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    meta: {
      title: "智慧校园"
    }
  },
  {
    path: "/Login",
    name: "Login",
    component: Login,
    meta: {
      title: "登陆"
    }
  },
  {
    path: "/Activity",
    name: "Activity",
    component: Activity,
    meta: {
      title: "校园活动"
    }
  },
  {
    path: "/Course",
    name: "Course",
    component: Course,
    meta: {
      title: "拓展课程"
    }
  },
  {
    path: "/Lost",
    name: "Lost",
    component: Lost,
    children: [
      {
        path: "/Lost/infind",
        name: "Lostinfind",
        component: Lostinfind,
        meta: {
          title: "失物招领"
        }
      },
      {
        path: "/Lost/add",
        name: "Lostadd",
        component: add,
        meta: {
          title: "失物招领"
        }
      }
    ]
  },
  {
    path: "/Appointment",
    name: "Appointment",
    component: Appointment,
    children: [
      {
        path: "/Appointment/Print",
        name: "AppointmentPrint",
        component: AppointmentPrint,
        meta: {
          title: "打印预约"
        }
      },
      {
        path: "/Appointment/Sport",
        name: "AppointmentSport",
        component: AppointmentSport,
        meta: {
          title: "运动场预约"
        }
      }
    ]
  },

  {
    path: "/Backstage",
    name: "Backstage",
    component: Backstage,
    meta: {
      title: "管理后台"
    },
    children: [
      //预约页路由
      {
        path: "/articles/makeAnAppointment/AppointmentSport",
        name: "makeAnAppointment_AppointmentSport",
        component: makeAnAppointment_AppointmentSport
      },
      {
        path: "/articles/makeAnAppointment/AppointmentPrint",
        name: "makeAnAppointment_AppointmentPrint",
        component: makeAnAppointment_AppointmentPrint
      },
      {
        path: "/articles/makeAnAppointment/core",
        name: "makeAnAppointment_core",
        component: makeAnAppointment_core
      },
      //活动页路由
      {
        path: "/articles/activity/administration",
        name: "activity_administration",
        component: activity_administration
      },
      {
        path: "/articles/activity/core",
        name: "activity_core",
        component: activity_core
      },
      //拓张课程页路由
      {
        path: "/articles/extendedCurriculum/administration",
        name: "extendedCurriculum_administration",
        component: extendedCurriculum_administration
      },
      {
        path: "/articles/extendedCurriculum/core",
        name: "extendedCurriculum_core",
        component: extendedCurriculum_core
      },
      //失物招领页路由
      {
        path: "/articles/findLostItems/administration",
        name: "findLostItems_administration",
        component: findLostItems_administration
      },
      {
        path: "/articles/findLostItems/core",
        name: "findLostItems_core",
        component: findLostItems_core
      },

      //账户管理页路由
      {
        path: "/articles/accountManagement/administration",
        name: "accountManagement_administration",
        component: accountManagement_administration
      }
    ]
  },
  {
    path: "/SetUp",
    name: "SetUp",
    component: SetUp,
    meta: {
      title: "个人设置"
    }
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
