<template>
  <el-form
    :model="ruleForm"
    :rules="rules"
    ref="ruleForm"
    label-width="100px"
    class="demo-ruleForm"
  >
    <el-form-item label="活动名称" prop="Sname">
      <el-input v-model="ruleForm.Sname"></el-input>
    </el-form-item>
    <el-form-item label="您的姓名" prop="Sstudentname">
      <el-input v-model="ruleForm.Sstudentname"></el-input>
    </el-form-item>
    <el-form-item label="联系电话" prop="Snumber">
      <el-input v-model="ruleForm.Snumber"></el-input>
    </el-form-item>
    <el-form-item label="班级" prop="Sclass">
      <el-input v-model="ruleForm.Sclass"></el-input>
    </el-form-item>
    <el-form-item label="活动区域" prop="Sregion">
      <el-select v-model="ruleForm.Sregion" placeholder="请选择活动区域">
        <el-option label="东门篮球场" value="东门篮球场"></el-option>
        <el-option label="羽毛球馆" value="羽毛球馆"></el-option>
        <el-option label="网球场" value="网球场"></el-option>
        <el-option label="乒乓球馆" value="乒乓球馆 "></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="活动时间" required>
      <el-col :span="11">
        <el-form-item prop="Sdate1">
          <el-date-picker
            v-model="ruleForm.Sdate1"
            type="datetime"
            placeholder="选择日期时间"
          >
          </el-date-picker>
        </el-form-item>
      </el-col>
    </el-form-item>
    <el-form-item label="预约性质" prop="Sresource">
      <el-radio-group v-model="ruleForm.Sresource">
        <el-radio label="集体活动"></el-radio>
        <el-radio label="私人使用"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="活动形式" prop="Sdesc">
      <el-input type="textarea" v-model="ruleForm.Sdesc"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm()">提交</el-button>
      <el-button @click="resetForm('ruleForm')">重置</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        Sname: "",
        Sstudentname: "",
        Snumber: "",
        Sclass: "",
        Sregion: "",
        Sdate1: "",
        Sresource: "",
        Sdesc: ""
      },
      rules: {
        //表单数据判断规则
        Sname: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        Sstudentname: [
          { required: true, message: "请输入姓名", trigger: "blur" }
        ],
        Sclass: [{ required: true, message: "请输入班级", trigger: "blur" }],
        Snumber: [
          { required: true, message: "请输入联系电话", trigger: "blur" }
        ],
        Sregion: [
          { required: true, message: "请选择活动区域", trigger: "change" }
        ],
        Sdate1: [
          {
            type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change"
          }
        ],
        Sresource: [
          { required: true, message: "请选择活动资源", trigger: "change" }
        ],
        Sdesc: [{ required: true, message: "请填写活动形式", trigger: "blur" }]
      }
    };
  },
  methods: {
    submitForm() {
      //提交表单
      this.$http
        .post("/api/appointmentsport", this.ruleForm)
        .then(res => {
          if (res.data) {
            this.$notify({
              title: "成功",
              message: "提交成功",
              type: "success"
            });
          } else {
            console.log("错误");
            this.$notify({
              message: "数据错误",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    resetForm(formName) {
      //重置表单
      this.$refs[formName].resetFields();
    }
  }
};
</script>
