<template>
  <div>
    <el-button type="mini" @click="refresh()">刷新</el-button>
    <el-table :data="tableData" style="width: 100%" height="600">
      <el-table-column prop="Ldate" label="日期" sortable width="180">
      </el-table-column>
      <el-table-column label="图片" width="180">
        <template slot-scope="scope">
          <el-image
            style="width: 100px; height: 100px"
            :src="tableData[scope.$index].Lurl"
            :preview-src-list="tableData[scope.$index].Lurl"
          >
          </el-image>
        </template>
      </el-table-column>
      <el-table-column prop="Lname" label="物品类型" width="180">
      </el-table-column>
      <el-table-column
        prop="Linfomation"
        label="物品信息"
        :formatter="formatter"
      >
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" type="success" @click="open(scope.$index)"
            >联系方式</el-button
          >
          <el-button
            v-if="stateis(scope.$index)"
            type="danger"
            icon="el-icon-delete"
            circle
            @click="changeit(scope.$index, tableData)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<style>
.image {
  width: 100px;
  height: 100px;
}
</style>
<script>
export default {
  data() {
    return {
      src:
        "http://img.whitelion233.top/images/2020/03/25/logoa104e2210c1ef437.png",
      tableData: []
    };
  },
  methods: {
    open(index) {
      //点击详情按钮后弹出详细信息。
      this.$alert(this.tableData[index].Lcontact, "联系方式", {
        confirmButtonText: "确定"
      });
    },
    stateis(index) {
      //判断是不是当前用户添加的数据，如果是则显示修改按钮，方便用户修改。
      if (this.tableData[index].Lid == localStorage.ID) return true;
      else return false;
    },
    changeit(index, rows) {
      //提交修改后的表单数据。
      this.$http
        .post("/api/loststate", this.tableData[index])
        .then(res => {
          if (res.data) {
            this.$notify({
              title: "成功",
              message: "提交成功",
              type: "success"
            });
            rows.splice(index, 1);
          } else {
            console.log("错误");
            this.$notify({
              message: "数据错误",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    refresh() {
      //刷新表单数据
      this.$http
        .post("/api/lost", this.tableData)
        .then(res => {
          if (res.data) {
            var obj = {};
            for (var i = 0; i < res.data.data.length; i++) {
              res.data.data[i].Ldate = res.data.data[i].Ldate.slice(0, 10);
              obj[i] = res.data.data[i];
              if (this.tableData[i].id != res.data.data[i].id) {
                this.tableData.push(obj[i]);
                console.log("已添加");
              } else console.log("数据重复");
            }

            console.log("更新成功");
          } else {
            console.log("错误");
            this.$notify({
              message: "更新失败",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    }
  },
  created: function() {
    //进入界面时执行的初始化函数，此处初始化表格数据。
    this.$http
      .post("/api/lost", this.tableData)
      .then(res => {
        if (res.data) {
          var obj = {};
          for (var i = 0; i < res.data.data.length; i++) {
            if (res.data.data[i].Lstate == 1) {
              if (this.tableData[i] != res.data.data[i]) {
                res.data.data[i].Ldate = res.data.data[i].Ldate.slice(0, 10);
                obj[i] = res.data.data[i];
                this.tableData.push(obj[i]);
              }
            }
          }
          console.log("更新成功");
        } else {
          console.log("错误");
          this.$notify({
            message: "更新失败",
            type: "warning"
          });
        }
      })
      .catch(rej => {
        this.$notify({
          message: rej,
          type: "warning"
        });
      });
  }
};
</script>
