<template>
  <el-form
    :model="ruleForm"
    :rules="rules"
    ref="ruleForm"
    label-width="100px"
    class="demo-ruleForm"
  >
    <el-form-item label="物品类型" prop="Lname">
      <el-input
        v-model="ruleForm.Lname"
        maxlength="10"
        minlength="3"
        type="text"
        show-word-limit
      ></el-input>
    </el-form-item>
    <el-form-item label="拾取时间" required>
      <el-col :span="11">
        <el-form-item prop="Ldate">
          <el-date-picker
            type="date"
            placeholder="选择日期"
            v-model="ruleForm.Ldate"
            style="width: 100%;"
          ></el-date-picker>
        </el-form-item>
      </el-col>
    </el-form-item>
    <el-form-item label="联系方式" prop="Lcontact">
      <el-input type="text" v-model="ruleForm.Lcontact"></el-input>
    </el-form-item>
    <el-form-item label="物品详情" prop="Linfomation">
      <el-input type="textarea" v-model="ruleForm.Linfomation"></el-input>
    </el-form-item>
    <el-form-item label="图片上传" prop="Limage">
      <el-upload
        class="upload-demo"
        drag
        :action="domain"
        :http-request="upqiniu"
        :show-file-list="false"
        :before-upload="beforeUpload"
        multiple
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">
          只能上传jpg/png文件，且不超过5M
        </div>
      </el-upload>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')"
        >立即创建</el-button
      >
      <el-button @click="resetForm('ruleForm')">重置</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      token: {},
      //七牛云的上传地址
      domain: "http://upload.qiniup.com/",
      //外链地址
      qiniuaddr: "qiniu.whitelion233.top",
      ruleForm: {
        Lname: "",
        Ldate: "",
        Lcontact: "",
        Linfomation: "",
        Lurl: "",
        Lid: ""
      },
      rules: {
        Lname: [
          { required: true, message: "请输入物品类型", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 10 个字符", trigger: "blur" }
        ],
        Ldate: [
          {
            type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change"
          }
        ],
        Lcontact: [
          { required: true, message: "请填写联系方式", trigger: "blur" }
        ],
        Linfomation: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      //提交表单
      this.ruleForm.Lid = localStorage.ID;
      this.$http
        .post("/api/lostadd", this.ruleForm)
        .then(res => {
          if (res.data) {
            this.$notify({
              title: "成功",
              message: "提交成功",
              type: "success"
            });
          } else {
            console.log("错误");
            this.$notify({
              message: "数据错误",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    resetForm(formName) {
      //重置表单
      this.$refs[formName].resetFields();
    },

    //七牛云上传操作**********************************************************************************
    upqiniu(req) {
      const config = {
        headers: { "Content-Type": "multipart/form-data" }
      };
      let filetype = "";
      if (req.file.type === "image/png") {
        filetype = "png";
      } else {
        filetype = "jpg";
      }
      //取得图片名
      let uploadImgName = req.file.name.substring(
        0,
        req.file.name.indexOf(".")
      );
      // 重命名要上传的文件
      const keyname =
        "cwh-imglist_" + uploadImgName + "_" + new Date().getTime();
      "." + filetype;
      // 从后端获取上传凭证token,此处在前端配置proxyTable代理进行跨域
      axios.get("/api/token").then(res => {
        const formdata = new FormData();
        formdata.append("file", req.file);
        formdata.append("token", res.data);
        formdata.append("key", keyname);
        // 获取到凭证之后再将文件上传到七牛云空间
        axios.post(this.domain, formdata, config).then(res => {
          this.ruleForm.Lurl = "http://" + this.qiniuaddr + "/" + res.data.key;
        });
      });
      this.$notify({
        title: "成功",
        message: "上传成功",
        type: "success"
      });
    },
    // 验证文件合法性
    beforeUpload(file) {
      const isJPG = file.type === "image/jpeg" || file.type === "image/png";
      const isLt2M = file.size / 1024 / 1024 < 5;
      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG、png 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传图片大小不能超过 5MB!");
      }
      return isJPG && isLt2M;
    }
  }
};
</script>
