<template>
  <el-form ref="form" :model="form" :rules="rules" label-width="100px">
    <el-form-item label="打印店选择" prop="Pregion">
      <el-select v-model="form.Pregion" placeholder="请选择打印店">
        <el-option label="打印店一" value="A店"></el-option>
        <el-option label="打印店二" value="B店"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="预约时间" prop="Pdate">
      <el-col :span="11">
        <el-form-item prop="Pdate">
          <el-date-picker
            v-model="form.Pdate"
            type="datetime"
            placeholder="选择日期时间"
          >
          </el-date-picker>
        </el-form-item>
      </el-col>
    </el-form-item>
    <el-form-item label="预打印" auto-upload="false">
      <el-switch v-model="form.Pdelivery"> </el-switch>
      <el-upload
        class="upload-demo"
        :action="domain"
        :http-request="upqiniu"
        :show-file-list="false"
        :before-upload="beforeUpload"
      >
        <el-button size="small" type="primary">点击上传文件</el-button>
      </el-upload>
    </el-form-item>
    <el-form-item type="primary" label="备注" placeholder="请填写联系方式。">
      <el-input type="textarea" v-model="form.Premark"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('form')">提交</el-button>
      <el-button>取消</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      token: {},
      //七牛云的上传地址
      domain: "http://upload.qiniup.com/",
      //外链地址
      qiniuaddr: "qiniu.whitelion233.top",
      form: {
        //表单数据存放、
        Pregion: "",
        Pdate: "",
        Pdelivery: false,
        Premark: "",
        Purl: ""
      },
      rules: {
        //判断表单合法的规则。
        Pregion: [
          { required: true, message: "请选择打印店", trigger: "change" }
        ],
        Pdate: [
          {
            type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change"
          }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      //提交函数，发出提交请求，传递表单信息。
      this.$http
        .post("/api/appointmentprint", this.form)
        .then(res => {
          if (res.data) {
            this.$notify({
              title: "成功",
              message: "提交成功",
              type: "success"
            });
          } else {
            console.log("错误");
            this.$notify({
              message: "数据错误",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //七牛云上传操作**********************************************************************************
    upqiniu(req) {
      const config = {
        headers: { "Content-Type": "multipart/form-data" }
      };
      //取得文件名
      let uploadImgName = req.file.name.substring(
        0,
        req.file.name.indexOf(".")
      );
      // 重命名要上传的文件
      const keyname = "cwh-print_" + uploadImgName + "." + "docx";
      // 从后端获取上传凭证token,此处在前端配置proxyTable代理进行跨域
      axios.get("/api/token").then(res => {
        const formdata = new FormData();
        formdata.append("file", req.file);
        formdata.append("token", res.data);
        formdata.append("key", keyname);
        // 获取到凭证之后再将文件上传到七牛云空间
        axios.post(this.domain, formdata, config).then(res => {
          this.form.Purl = "http://" + this.qiniuaddr + "/" + res.data.key;
        });
      });
      this.$notify({
        title: "成功",
        message: "上传成功",
        type: "success"
      });
    },
    // 验证文件合法性
    beforeUpload(file) {
      const isJPG =
        file.type ===
        "application/vnd.openxmlformats-officedocument.wordprocessingml.document";
      if (!isJPG) {
        this.$message.error("上传文件只能是docx格式!");
      }
    }
  }
};
</script>
