<template>
  <div>
    <el-header style="text-align: right; font-size: 12px">
      <div class="LogoAP"></div>
      <div>
        <el-button
          type="info"
          icon="el-icon-s-home"
          @click="homepage()"
          circle
        ></el-button>
      </div>
    </el-header>

    <el-tabs type="border-card">
      <el-tab-pane label="基本资料">
        <div style="width: 100%;height:600px;">
          <div>
            <el-row :gutter="0">
              <el-col :span="8">
                <!--  -->
                <el-card> 昵称：{{ userlist[0].username }} </el-card>
              </el-col>
              <el-col :span="16"> </el-col>
            </el-row>
          </div>
          <div>
            <el-row :gutter="0">
              <el-col :span="8">
                <!--  -->
                <el-card> 邮箱地址：{{ userlist[0].Email }} </el-card>
              </el-col>
              <el-col :span="16">
                <!--  -->
              </el-col>
            </el-row>
          </div>
          <div>
            <el-row :gutter="0">
              <el-col :span="8">
                <!--  -->
                <el-card> 学号：{{ userlist[0].ID }} </el-card>
              </el-col>
              <el-col :span="16">
                <!--  -->
              </el-col>
            </el-row>
          </div>
          <!--  -->
          <el-button type="primary" @click="showEditDialog()"
            >修改昵称</el-button
          >
        </div>
      </el-tab-pane>
      <el-tab-pane label="修改头像">
        <div style="width: 100%;height:600px;">
          <el-form
            :model="password"
            :rules="rules"
            ref="userlistAddRef"
            label-width="100px"
            style="width: 60%;"
          >
            <el-upload
              style="margin-left:50px;"
              class="upload-demo"
              drag
              :action="domain"
              :http-request="upqiniu"
              :show-file-list="false"
              :before-upload="beforeUpload"
              multiple
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                将文件拖到此处，或<em>点击上传</em>
              </div>
              <div class="el-upload__tip" slot="tip" style="margin-left:80px;">
                只能上传jpg/png文件，且不超过5M
              </div>
            </el-upload>
            <span style="font-size:2em;"
              >------------------当前头像------------------</span
            >
            <div class="demo-image__preview" style="margin-left:100px;">
              <el-image
                style="width: 250px; height: 200px"
                :src="imageurl"
                :preview-src-list="srcList"
              >
              </el-image>
            </div>
            <el-button type="primary" @click="submitForm()">确认</el-button>
          </el-form>
        </div>
      </el-tab-pane>
      <el-tab-pane label="修改密码">
        <div style="width: 100%;height:600px;">
          <el-form
            :model="password"
            :rules="rules"
            ref="userlistAddRef"
            label-width="100px"
            style="width: 60%;"
          >
            <el-form-item label="旧密码" prop="Apassword">
              <el-input v-model="password.Apassword"></el-input>
            </el-form-item>
            <el-form-item label="新密码" prop="B1password">
              <el-input v-model="password.B1password"></el-input>
            </el-form-item>
            <el-form-item label="确认新密码" prop="B2password">
              <el-input v-model="password.B2password"></el-input>
            </el-form-item>
            <el-button type="primary" @click="BsubmitForm()">确认</el-button>
          </el-form>
        </div>
      </el-tab-pane>
    </el-tabs>
    <!-- 修改昵称 -->
    <el-dialog
      title="修改昵称"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!-- 内容主体 -->
      <span>
        <el-form
          :model="Nickname"
          :rules="rules"
          ref="userlistAddRef"
          label-width="100px"
          style="width: 60%;"
        >
          <el-form-item label="新昵称" prop="Nickname">
            <el-input v-model="Nickname"></el-input>
          </el-form-item>
        </el-form>
      </span>
      <!-- 底部内容 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button
          type="primary"
          @click="(editDialogVisible = false), submitnickname()"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<style>
.LogoAP {
  width: 150px;
  height: 80px;
}
</style>

<script>
import axios from "axios";
export default {
  data() {
    //验证确认密码的规则
    var checkpassword = (rule, value, cd) => {
      if (value == this.password.B1password) {
        //合法的确认密码
        return cd();
      }
      cd(new Error("请输入一致的确认密码"));
    };
    var checkoldpassword = (rule, value, cd) => {
      if (value == this.userlist[0].password) {
        return cd();
      }
      cd(new Error("旧密码不正确"));
    };

    return {
      src:
        "http://img.whitelion233.top/images/2020/03/25/logoa104e2210c1ef437.png",
      token: {},
      //七牛云的上传地址
      domain: "http://upload.qiniup.com/",
      //外链地址
      qiniuaddr: "qiniu.whitelion233.top",
      userlist: [],

      imageUrl: "",
      Nickname: "",
      password: {
        Apassword: "",
        B1password: "",
        B2password: ""
      },

      //修改界面的显示&&隐藏
      editDialogVisible: false,

      rules: {
        Apassword: [
          { required: true, message: "请输入旧密码", trigger: "blur" },
          { validator: checkoldpassword, trigger: "blur" }
        ],
        B1password: [
          { required: true, message: "请输入新密码", trigger: "blur" },
          { min: 6, max: 15, message: "长度在 6 到 15 个字符", trigger: "blur" }
        ],
        B2password: [
          { required: true, message: "请输入确认新密码", trigger: "blur" },
          { validator: checkpassword, trigger: "blur" }
        ],
        Nickname: [
          { required: true, message: "请输入新昵称", trigger: "blur" },
          { min: 6, max: 15, message: "长度在 6 到 15 个字符", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    BsubmitForm(formName) {
      const id = localStorage.ID / 2 - 2020514 + 2020;
      var test = {
        password: this.password.B2password,
        ID: id
      };
      //数据库修改，发送数据到后端
      this.$http
        .post("/api/setuppassword", test)
        .then(res => {
          if (res.data) {
            this.$router.push("/SetUp");
            this.$notify({
              title: "成功",
              message: "更改成功",
              type: "success"
            });
          } else {
            this.$notify({
              message: "数据错误",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    showEditDialog() {
      this.editDialogVisible = true;
    },
    submitnickname() {
      const id = localStorage.ID / 2 - 2020514 + 2020;
      var test = {
        username: this.Nickname,
        ID: id
      };
      //数据库修改，发送数据到后端
      this.$http
        .post("/api/setupname", test)
        .then(res => {
          if (res.data) {
            this.$router.push("/SetUp");
            this.$notify({
              title: "成功",
              message: "上传成功",
              type: "success"
            });
          } else {
            console.log("错误");
            this.$notify({
              message: "数据错误",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    homepage() {
      const loading = this.$loading({
        lock: true,
        text: "正在加载",

        spinner: "el-icon-loading"
      });
      setTimeout(() => {
        loading.close();
        this.$router.push("/");
      }, 1000);
    },
    submitForm(formName) {
      const id = localStorage.ID / 2 - 2020514 + 2020;
      //提交图片头像修改
      var test = {
        ID: id,
        url: this.imageUrl
      };
      this.$http
        .post("/api/setupurl", test)
        .then(res => {
          if (res.data) {
            localStorage.url = this.imageUrl;
            this.$notify({
              title: "成功",
              message: "上传成功，下一次登陆时生效",
              type: "success"
            });
          } else {
            console.log("错误");
            this.$notify({
              message: "数据错误",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //七牛云上传操作**********************************************************************************
    upqiniu(req) {
      const config = {
        headers: { "Content-Type": "multipart/form-data" }
      };
      let filetype = "";
      if (req.file.type === "image/png") {
        filetype = "png";
      } else {
        filetype = "jpg";
      }
      //取得图片名
      let uploadImgName = req.file.name.substring(
        0,
        req.file.name.indexOf(".")
      );
      // 重命名要上传的文件
      const keyname =
        "cwh-imglist_" + uploadImgName + "_" + new Date().getTime();
      "." + filetype;
      // 从后端获取上传凭证token,此处在前端配置proxyTable代理进行跨域
      axios.get("/api/token").then(res => {
        const formdata = new FormData();
        formdata.append("file", req.file);
        formdata.append("token", res.data);
        formdata.append("key", keyname);
        // 获取到凭证之后再将文件上传到七牛云空间
        axios.post(this.domain, formdata, config).then(res => {
          this.imageUrl = "http://" + this.qiniuaddr + "/" + res.data.key;
        });
      });
      this.$notify({
        title: "成功",
        message: "上传成功",
        type: "success"
      });
    },
    // 验证文件合法性
    beforeUpload(file) {
      const isJPG = file.type === "image/jpeg" || file.type === "image/png";
      const isLt2M = file.size / 1024 / 1024 < 5;
      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG、png 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传图片大小不能超过 5MB!");
      }
      return isJPG && isLt2M;
    }
  },
  created: function() {
    var ID = localStorage.ID / 2 - 2020514 + 2020;
    var url = localStorage.url;
    if (url != null) {
      this.imageurl = url;
    } else
      this.imageurl =
        "http://img.whitelion233.top/images/2020/04/17/u38083990471382703100fm26gp0.png";
    var goin = {
      ID: ID
    };
    this.$http
      .post("/api/setupfind", goin)
      .then(res => {
        if (res.data) {
          var obj = {};
          for (var i = 0; i < res.data.data.length; i++) {
            obj[i] = res.data.data[i];
            this.userlist.push(obj[i]);
          }
          console.log("更新成功");
        } else {
          console.log("错误");
          this.$notify({
            message: "更新失败",
            type: "warning"
          });
        }
      })
      .catch(rej => {
        this.$notify({
          message: rej,
          type: "warning"
        });
      });
  }
};
</script>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>
